.FormField__border {
	border: none;
}

input {
	margin: 0;
	width: 100%;
	display: block;
	box-sizing: border-box;
	box-shadow: none;
	border: 0;
	outline: 0;
	-webkit-appearance: none;
	-moz-appearance: none;
	     appearance: none;
	text-overflow: ellipsis;
	height: 44px;
	line-height: 19px;
	font-size: 16px;
	color: #000;
	color: #000000;
	color: var(--text_primary);
	padding: 12px;
	position: relative;
	z-index: 2;
	background: 0 0;
}

.Message {
	position: relative;
	line-height: 24px;
	margin: 5px;
	padding: 5px;
	background-color: #e4e4e4;
	color: black;
	max-width: 60%;
	border-radius: 15px;
}

.MessagesContent {
	padding-bottom: 60px;
}

